* {
    font-family: JetBrainsMonoNL-Thin;
    src: url("./fonts/JetBrainsMonoNL-Thin.ttf");
    font-weight: lighter;
}

@font-face {
    font-family: 'JetBrainsMonoNL-Thin';
    src: url('./fonts/JetBrainsMonoNL-Thin.eot');
    src: url('./fonts/JetBrainsMonoNL-Thin.eot#iefix') format('embedded-opentype'),
        url('./fonts/JetBrainsMonoNL-Thin.woff') format('woff'),
        url('./fonts/JetBrainsMonoNL-Thin.ttf') format('truetype'),
        url('./fonts/JetBrainsMonoNL-Thin.svg') format('svg');
    font-weight: lighter;
    font-style: normal;
}

a {
    text-decoration: none;
    color: inherit;
}

body {
    padding-top: 4em;
    text-align: center;
    background-color: #212529;
    overflow-x: hidden;
}

button {
    background-color: #D63F09;
    border-width: 0;
    border-radius: 15px;
    padding: 10px;
    margin: 15px;
    color: #ececec;
    font-size: 18px;
}

h1 {
    font-style: bold;
    margin: 10px;
}

h2 {
    font-style: italic;
    margin-bottom: 10px;
    margin-top: 10px
}

p,
h2 {
    margin-left: 10%;
    margin-right: 10%;
}

.archive-dropdown {
    background-color: white;
    color: black;
}

.carousel-images {
    width: 100%;
    height: auto;
    max-width: 350px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 40px;
    display: block;
}


.carousel-item {
    margin-bottom: 100px;
}

.carousel-section-header {
    font-weight: bold;
    margin-bottom: 20px;
    margin-top: 30px;
}


.footer_icons {
    height: 30px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 30px;
    margin-bottom: 20px;
}

.hackathon-title {
    padding-top: 10px;
    padding-bottom: 10px;
}

.infobody {
    background-color: white;
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 3%;
    margin-bottom: 3%;
    padding-top: 25px;
    padding-bottom: 25px;
    border-radius: 15px;
    box-shadow: 0px 2px 10px 1px rgb(220, 220, 220);
}

@media screen and (max-width: 600px) {
    .infobody {
        margin-left: 3%;
        margin-right: 3%;
    }
}


.joinclubbuttons {
    background-color: #D63F09;
    margin-left: 0px;
    margin-right: 0px;
}


.navbar-dark .navbar-nav .nav-link {
    color: white;
    font-size: 20px;
    margin-left: 20px;
}

.officer-name {
    padding-top: 20px;
    color: #DA704A;
    font-size: 20px;
    text-align: left;
    margin-left: 0px;

}

.officer-title {
    padding-top: 20px;
    font-style: italic;
    font-size: 20px;
    text-align: right;
    margin-right: 0px;

}

.orange-body-text {
    color: #DA704A;
    font-size: 20px;
}

.pagebody {
    background-color: #ECECEC;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 15px;
    padding-bottom: 30px;
    padding-top: 20px;
    box-shadow: 0px 2px 10px 1px rgb(220, 220, 220);
    margin-bottom: 30px;
}

.pagetitle {
    background-color: #DA704A;
    color: white;
    border-radius: 15px;
    padding-bottom: 40px;
    padding-top: 50px;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 40px;
    letter-spacing: 10px;
    box-shadow: 0px 2px 10px 1px rgb(220, 220, 220);
}

.past_themes {
    font-size: 30px;
    color: #DA704A
}

.place-title {
    padding-top: 40px;
}

.archive-section {
    border-radius: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-bottom: 40px;
    margin-left: 3rem;
    margin-right: 3rem;
    margin-top: 20px;
    margin-bottom: 20px;
}

#beaverhacks {
    font-size: 40px;
    color: white;
    letter-spacing: 0px;
}

#circlelogo {
    width: 100%;
    height: auto;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 50px;
    display: block;
}

#dropdown-button {
    background-color: #DA704A;
    border: #DA704A;
}

#footer_text_club {
    font-style: italic;
    font-size: 15px;
    color: #A0A0A0;
}

.background-gradient {
    background: #212529;
    width: auto;
    height: 100vh;
}

.smallText{
    margin: auto;
    color: rgb(255, 115, 0);
    padding-top: 20px;
    padding-bottom: 20px;
}

.bigText {
    padding-top: 150px;
    font-size: 10vw;
    background-image: linear-gradient(90deg,
            rgb(255, 115, 0) 0%,
            rgb(255, 212, 177) 15%,
            rgb(255, 115, 0) 30%,
            rgb(255, 115, 0) 100%);
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 4s linear infinite;
    display: inline-block;
    animation-direction: reverse;
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}